import Vue from 'vue'
import Router, {
	RouterMount
} from 'uni-simple-router';
import modules from './modules'
Vue.use(Router)
//初始化
const router = new Router({
	routes: [...modules]//路由表
});
let token = ''
uni.getStorage('token').then(res=>token=res.data)

//全局路由前置守卫
router.beforeEach(async (to, from, next) => {
	if(to.path!=='/pages/login/login'){
		next({
			path: '/pages/login/login',
			query: {
				msg: '我拦截了tab5并重定向到了路由4页面上',
			},
			NAVTYPE: 'push'
		});
	} else {
		// next({
		// 	name: 'login',
		// 	params: {
		// 		msg: '我拦截了tab5并重定向到了路由4页面上',
		// 	},
		// 	NAVTYPE: 'push'
		// })
		next()
	}

})
// 全局路由后置守卫
router.afterEach((to, from) => {
	console.log('afterEach----守卫')
})

export {
	RouterMount,
	router
}
